import React, { useState } from "react";
import {
  Paper,
  Container,
  Grid,
  Avatar,
  List,
  ListItem,
  IconButton,
  ListSubheader,
  Modal,
  Typography,
  Divider,
  ExpansionPanel,
} from "@material-ui/core";
import { Skeleton } from "@material-ui/lab";
import { Edit, ArrowRight } from "@material-ui/icons";
import { Image } from "semantic-ui-react";
import { Responsive } from "semantic-ui-react";
import { grey } from "@material-ui/core/colors";

const Profile = (props) => {
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "center",
        marginTop: 10,
      }}
    >
      <Responsive {...Responsive.onlyComputer}>
        <ProfileInfo
          editable={props.editable}
          {...props}
          deviceType={"com"}
        ></ProfileInfo>
      </Responsive>
      <Responsive {...Responsive.onlyTablet}>
        <ProfileInfo
          editable={props.editable}
          {...props}
          deviceType={"tab"}
        ></ProfileInfo>
      </Responsive>
      <Responsive {...Responsive.onlyMobile}>
        <ProfileInfo
          editable={props.editable}
          {...props}
          deviceType={"mob"}
        ></ProfileInfo>
      </Responsive>
    </div>
  );
};
const ListHeaderStyle = {
  fontFamily: "poppins",
  fontWeight: "bold",
  color: "grey",
  fontSize: 16,
};
const ImageModal = (props) => {
  const { editable, src, open, setModal } = props;

  return (
    <Modal
      open={open}
      onClose={() => {
        setModal(false);
      }}
      style={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
      }}
    >
      <Paper style={{ background: grey[900] }}>
        <Image src={src} size="huge"></Image>
        {editable ? (
          <List>
            <ListSubheader style={ListHeaderStyle}>
              <IconButton>
                <Edit style={{ color: "white" }}></Edit>
              </IconButton>
              Edit
            </ListSubheader>
          </List>
        ) : (
          ""
        )}
      </Paper>
    </Modal>
  );
};

const ProfileInfo = (props) => {
  const [openModal, setOpenModal] = useState(false);
  const [imgSrc, setSrc] = useState("pp");
  const [ppl, setPPl] = useState(false);
  const [bpl, setBPl] = useState(false);
  const calculateWidth = (type) => {
    switch (type) {
      case "com":
        return {
          width: 700,
        };
      case "tab":
        return {
          width: 500,
        };
      case "mob":
        return {
          width: 350,
        };
      default:
        return {
          width: 500,
        };
    }
  };
  const dividerBack = grey[800];
  return (
    <div>
      <ImageModal
        open={openModal}
        editable={props.editable}
        setModal={setOpenModal}
        src={imgSrc === "pp" ? props.profilephoto : props.backgroundPhoto}
      ></ImageModal>
      <Paper
        elevation={10}
        style={{
          ...calculateWidth(props.deviceType),
          borderRadius: 10,
          background: grey[900],
        }}
      >
        <Skeleton
          hidden={bpl}
          variant="rect"
          width={"100%"}
          style={{
            color: "white",
            background: "grey",
            borderTopLeftRadius: 10,
            borderTopRightRadius: 10,
          }}
          height={300}
        ></Skeleton>
        <Image
          hidden={!bpl}
          src={props.backgroundPhoto}
          style={{
            width: "100%",
            maxHeight: 300,
            borderTopLeftRadius: 10,
            borderTopRightRadius: 10,
            cursor: "pointer",
          }}
          onClick={() => {
            setOpenModal(true);
            setSrc("bp");
          }}
          onLoad={() => {
            setBPl(true);
          }}
        ></Image>

        <Skeleton
          hidden={ppl}
          variant="circle"
          width={100}
          height={100}
          style={{
            color: "white",
            background: "lightgrey",
            transform: "translateY(-50px) translateX(20px)",
          }}
        ></Skeleton>

        <Avatar
          hidden={!ppl}
          src={props.profilephoto}
          style={{
            width: 100,
            height: 100,
            transform: "translateY(-50px) translateX(20px)",
            cursor: "pointer",
          }}
          onClick={() => {
            setOpenModal(true);
            setSrc("pp");
          }}
          onLoad={() => {
            setPPl(true);
          }}
        ></Avatar>

        <List style={{ marginLeft: 10, marginRight: 10 }}>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: "bold",
              fontFamily: "poppins",
              transform: "translateY(-60px)",
              color: "white",
            }}
          >
            {props.username}
          </ListSubheader>

          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Name
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.firstName + " " + props.LastName}
            </Typography>
            {props.editable ? (
              <IconButton
                style={{
                  float: "right",
                  color: "white",
                  transform: "translateY(-30px)",
                }}
              >
                <Edit></Edit>
              </IconButton>
            ) : (
              ""
            )}
          </ListSubheader>
          <Divider
            style={{ color: "grey", background: dividerBack, marginTop: 10 }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Gender
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.gender}
            </Typography>
            {props.editable ? (
              <IconButton
                style={{
                  float: "right",
                  color: "white",
                  transform: "translateY(-30px)",
                }}
              >
                <Edit></Edit>
              </IconButton>
            ) : (
              ""
            )}
          </ListSubheader>
          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Course
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.course}
            </Typography>
            {props.editable ? (
              <IconButton
                style={{
                  float: "right",
                  color: "white",
                  transform: "translateY(-30px)",
                }}
              >
                <Edit></Edit>
              </IconButton>
            ) : (
              ""
            )}
          </ListSubheader>
          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Stream
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.Branch}
            </Typography>
            {props.editable ? (
              <IconButton
                style={{
                  float: "right",
                  color: "white",
                  transform: "translateY(-30px)",
                }}
              >
                <Edit></Edit>
              </IconButton>
            ) : (
              ""
            )}
          </ListSubheader>

          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Roll_No
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                fontFamily: "poppins",
              }}
            >
              {props.rollno}
            </Typography>
            {props.editable ? (
              <IconButton
                style={{
                  float: "right",
                  color: "white",
                  transform: "translateY(-30px)",
                }}
              >
                <Edit></Edit>
              </IconButton>
            ) : (
              ""
            )}
          </ListSubheader>
          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            College
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                marginBottom: 10,
                fontFamily: "poppins",
              }}
            >
              {props.college}
            </Typography>
          </ListSubheader>
          <Divider
            style={{
              color: "lightgrey",
              background: dividerBack,
              marginTop: 10,
            }}
          ></Divider>
          <ListSubheader
            disableSticky
            style={{
              fontSize: 20,
              fontWeight: 500,
              color: "grey",
              fontFamily: "poppins",
            }}
          >
            Year Of Joining College
            <Typography
              style={{
                color: "white",
                marginLeft: 10,
                marginBottom: 10,
                fontFamily: "poppins",
              }}
            >
              {props.yearofjoining}
            </Typography>
          </ListSubheader>

          {props.children}
        </List>
      </Paper>
    </div>
  );
};

export default Profile;
